<template>
  <div class="content" id="gameDiv">

    <b-container class="bv-example-row">
      <b-row>
        <b-col> <tic-tac-toe :key="componentKey"/> <br/> <b-button v-on:click="forceRerender()">Recommencer</b-button> </b-col>
        <b-col></b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import Vue from 'vue'
import TicTacToe from '@/components/game/TicTacToe';
import Cell from '@/components/game/Cell';

Vue.component('tic-tac-toe', TicTacToe);
Vue.component('cell', Cell);
export default {
  data() {
   return {
     componentKey: 0
   }
 },
   methods: {
   forceRerender() {
     this.componentKey += 1
   }
 }
}
</script>

