import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from "@/components/content/Home.vue";
import Scores from "@/components/content/Scores.vue";
import SinglePlayer from "@/components/content/SinglePlayer.vue";
import Multiplayer from "@/components/content/Multiplayer.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/scores',
    name: 'scores',
    component: Scores
  },
  {
    path: '/singleplayer',
    name: 'singleplayer',
    component: SinglePlayer
  },
  {
    path: '/multiplayer',
    name: 'multiplayer',
    component: Multiplayer
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
