<template>
  <div class="tictactoe-board">
    <div v-for="(n, i) in 3"  :key="i">
      <div v-for="(n, j) in 3" :key="j">
        <cell @click="performMove(j, i)" :value="board.cells[j][i]"></cell>
      </div>
    </div>

    <div class="game-over-text" v-if="gameOver">
      {{ gameOverText }}
    </div>
  </div>
</template>
<script>
  import Board from "./Board";
  export default {
    data() { return {
      gameOver: false,
      gameOverText: '',
      board: new Board(),
      playerValue: 'x'
    } },
    methods: {
      performMove(x, y) {
        if (this.gameOver) {
          return;
        }
        if (! this.board.doMove(x, y, this.playerValue)) {
          return;
        }
        this.$forceUpdate();
        if (this.board.isGameOver()) {
          this.gameOver = true;
          this.gameOverText = this.board.playerHas3InARow(this.playerValue) ? 'Gagn√©!' : 'Match nul';
          return;
        }
        if (this.playerValue == 'o'){
          this.playerValue = 'x';
        } else {
          this.playerValue = 'o';
        }
      },
    }
  }
</script>
<style>
  .tictactoe-board {
    display: flex;
    flex-wrap: wrap;
    width: 204px;
    height: 204px;
  }
</style>
